<script>
import Layout from "@/components/common/Layout";


import '@/lib/jquery/jquery.min.js';
import '@/assets/js/common-9feb83e959.js';
import '@/assets/js/about-9de3af17fa.js';


export default {
  name: "AboutView",
  components: {Layout},
  data() {
    return {
      companyInfo: {},
      designers: [],
    }
  },
  mounted() {
    this.getCompanyInfo()
    this.getDesigners()
  },
  methods: {
    getCompanyInfo() {
      this.getRequest("/findCompanyInfo").then(resp => {
        if (resp) {
          this.companyInfo = resp.data.data
        }
      })
    },
    getDesigners() {
      this.getRequest("/findDesigners").then(resp => {
        if (resp) {
          this.designers = resp.data.data
        }
      })
    }

  }
}


