<script type='text/javascript'>
$(function () {
  var honor = {
    "2016": [{
      "text": "<p>Innos obtained the National High-Tech Enterprise Qualification.</p><p>Innos obtained ISO9001 International Quality System Certification.</p>"
    }],
    "2017": [{
      "text": "Innos obtained Chinese Intellectual Property Management System Certification."
    }],
    "2018": [{
      "text": "Window Robot Cleaner W902 was selected as “The Model of the Design in Modern Chinese Design Exhibition”."
    },
      {
        "text": "Innos robot vacuum cleaner were selected and displayed in the largest Red Dot Museum of the world."
      }
    ],
    "2020": [{
      "text": "Innos robot vacuum cleaner CV520C won the Red Dot Award."
    }]
  };
  var timer;
  var idx = 0;
  $(".honor-aItem").on("mouseenter", function () {
    clearInterval(timer);
    var index = $(this).parent('p').index() - 1;
    idx = index;
    var year = $(this).parents(".honor-item-con").data('year');
    $(this).parents(".honor-item-con").children("img").attr("src", "../assets/images/about/honor/" + year + "_" + index + ".jpg");
    $(this).addClass('bl-color-_333').parent().siblings().children('.honor-aItem').removeClass('bl-color-_333');
  });
  $('.honor-aItem').on('mouseleave', function () {
    var len = $(this).parents(".honor-item-con").find("p").length;
    var self = $(this)
    if (len > 1) {
      timer = setInterval(function () {
        timeFun(self.parents(".honor-item-con"));
      }, 3000);
    }
  });

  $('.honor-item').click(function () {
    clearInterval(timer);
    idx = 0;
    $(this).addClass('active').siblings().removeClass('active');
    var year = $(this).find('.honor-item-con').data('year');
    $(this).find('.honor-item-con img').attr('src', "../assets/images/about/honor/" + year + "_" + "0.jpg");
    $(this).find("p").eq(0).children(".honor-aItem").addClass('bl-color-_333').parent().siblings().children('.honor-aItem').removeClass(
        'bl-color-_333');
    var childLen = $(this).find(".honor-item-con p").length;
    var self = $(this)
    if (childLen > 1) {
      timer = setInterval(function () {
        timeFun(self.find('.honor-item-con'));
      }, 3000);
    }

  });

  function timeFun(el) {
    var childLen = el.find('p').length;
    var year = el.data('year');
    idx++;
    if (idx >= childLen) idx = 0;
    el.find('img').attr('src', "../assets/images/about/honor/" + year + "_" + idx + ".jpg");
    el.find("p").eq(idx).children(".honor-aItem").addClass('bl-color-_333').parent()
        .siblings().children('.honor-aItem').removeClass('bl-color-_333');
  };
  timer = setInterval(function () {
    timeFun($(".honor-list .honor-item").eq(0).children(".honor-item-con"));
  }, 3000);
})
</script>


